            <li class="tabs__item">
              <a class="tabs__link" href="#"> Характеристики </a>
            </li>
            <li class="tabs__item">
              <a class="tabs__link" href="#"> Гарантия </a>
            </li>
            <li class="tabs__item">
              <a class="tabs__link" href="#"> Оплата и доставка </a>
            </li>
          </ul>

          <div class="item__content">
            <p>
              Навигация GPS, ГЛОНАСС, BEIDOU Galileo и QZSS<br />
              Синхронизация со смартфоном<br />
              Связь по Bluetooth Smart, ANT+ и Wi-Fi<br />
              Поддержка сторонних приложений<br />
            </p>

            <a href="#"> Все характеристики </a>

            <h3>Что это?</h3>

            <p>
              Wahoo ELEMNT BOLT GPS – это велокомпьютер, который позволяет
              оптимизировать свои велотренировки, сделав их максимально
              эффективными. Wahoo ELEMNT BOLT GPS синхронизируется с датчиками
              по ANT+, объединяя полученную с них информацию. Данные
              отображаются на дисплее, а также сохраняются на смартфоне. При
              этом на мобильное устройство можно установить как фирменное
              приложение, так и различные приложения сторонних разработчиков.
              Велокомпьютер точно отслеживает местоположение, принимая сигнал с
              целого комплекса спутников. Эта информация позволяет смотреть уже
              преодоленные маршруты и планировать новые велопрогулки.
            </p>

            <h3>Дизайн</h3>

            <p>
              Велокомпьютер Wahoo ELEMNT BOLT очень компактный. Размеры
              устройства составляют всего 74,6&times;47,3&times;22,1&nbsp;мм.
              что не&nbsp;превышает габариты смартфона. Корпус гаджета выполнен
              из&nbsp;черного пластика. На&nbsp;обращенной к&nbsp;пользователю
              стороне расположен дисплей диагональю 56&nbsp;мм. На&nbsp;дисплей
              выводятся координаты и&nbsp;скорость, а&nbsp;также полученная
              со&nbsp;смартфона и&nbsp;синхронизированных датчиков информация:
              интенсивность, скорость вращения педалей, пульс и&nbsp;т.д.
              (датчики не входят в&nbsp;комплект поставки). Корпус
              велокомпьютера имеет степень защиты от&nbsp;влаги IPX7. Это
              означает, что устройство не&nbsp;боится пыли, а также выдерживает
              кратковременное (до&nbsp;30&nbsp;минут) погружение в&nbsp;воду на
              глубину не&nbsp;более 1&nbsp;метра.
            </p>
          </div>
        </div>
      </section>
    </main>
  </div>
</template>
<script>
import gotoPage from "@/helpers/gotoPage";
import numberFormat from "@/helpers/numberFormat";
import axios from "axios";
import { API_BASE_URL } from "@/../config";
import { mapActions } from 'vuex'

export default {
  data() {
    return {
      productAmount: 1,
      productData: null,
      productLoading: false,
      productLoadingFailed: false,

      productAdded: false,
      productAddSending: false
    };
  },
  filters: {
    numberFormat,
  },
  computed: {
    product() {
      return this.productData;
    },
    category() {
      return this.productData.category;
    },
    colors() {
      return this.productData.colors;
    },
  },
  methods: {
    ...mapActions(['addProductToCart']),

    gotoPage,
    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;

      this.addProductToCart({
        productId: this.product.id,
        amount: this.productAmount,
      })
      .then(() => {
        this.productAdded = true;
      this.productAddSending = false;
      });
    },
    incrementAmount() {
      this.productAmount = this.productAmount + 1;
    },
    discrementAmount() {
      if (this.productAmount >= 1) {
        this.productAmount = this.productAmount - 1;
      }
    },
    loadProduct() {
      this.productLoading = true;
      this.productLoadingFailed = false;
      axios
        .get(API_BASE_URL + "/api/products/" + this.$route.params.id)
        .then((response) => (this.productData = response.data))
        .catch(() => (this.productLoadingFailed = true))
        .then(() => (this.productLoading = false));
    },
  },
  created() {
    this.loadProduct();
  },
  watch: {
    "$route.params.id": {
      handler() {
        this.loadProduct();
      },
      immediate: true,
    },
  },
};
</script>
