<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html"> Каталог </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html"> Корзина </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Оформление заказа </a>
        </li>
      </ul>

      <h1 class="content__title"> Корзина </h1>
      <span class="content__info"> 3 товара </span>
    </div>

    <section class="cart">
      <form
        class="cart__form form"
        action="#"
        method="POST"
        @submit.prevent="order"
      >
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText
              title="ФИО"
              :error="formError.name"
              v-model="formData.name"
              placeholder="Введите ваше полное имя"
            />
            <BaseFormText
              title="Адрес доставки"
              :error="formError.address"
              v-model="formData.address"
              placeholder="Введите ваш адресс"
            />
            <BaseFormText
              title="Телефон"
              :error="formError.phone"
              v-model="formData.phone"
              placeholder="Введите ваш телефон"
            />
            <BaseFormText
              title="Email"
              :error="formError.email"
              v-model="formData.email"
              placeholder="Введите ваш Email"
            />
            <BaseFormTextarea
              title="Комментарий к заказу"
              :error="formError.comment"
              v-model="formData.comment"
              placeholder="Ваши пожелания"
            />
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options"  >
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="0" 
                    checked 
                  />
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="500" 
                  />
                  <span class="options__value"> Курьером <b>500 ₽</b> </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    value="card" 
                  />
                  <span class="options__value"> Картой при получении </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    value="cash" 

                  />
                  <span class="options__value"> Наличными при получении </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li
              class="cart__order"
              v-for="item in products"
              :key="item.productId"
            >
              <h3>{{ item.product.title }}</h3>
              <b>{{ (item.amount * item.product.price) | numberFormat }} ₽</b>
              <span>Артикул: {{ item.product.id }}</span>
            </li>
             
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b> {{ $store.state.cartProducts.length }} </b> товара на сумму <b>{{  totalPrice | numberFormat }} ₽</b></p>
          </div>

          <button
            class="cart__button button button--primery"
            type="submit"
            v-if="!orderLoading"
          >
            Оформить заказ
          </button>
          <button
            class="cart__button button button--primery"
            type="submit"
            v-if="orderLoading"
          >
            Идет процесс...<img src="img/spinner.gif" />
          </button>
        </div>
        <div
          class="cart__error form__error-block"
          v-if="formErrorMessage && orderLoadingFailed"
        >
          <h4>Заявка не оформлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>
<script>
import BaseFormText from "@/components/BaseFormText";
import BaseFormTextarea from "@/components/BaseFormTextarea";
import axios from "axios";
import { API_BASE_URL } from "@/../config";
import numberFormat from "@/helpers/numberFormat";
import { mapGetters } from "vuex";

export default {
  components: { BaseFormText, BaseFormTextarea },
  filters: { numberFormat },
  methods: {
    order() {
      this.formError = {};
      this.formErrorMessage = "";
      this.orderLoading = true;
      this.orderLoadingFailed = false;

      clearTimeout(this.loadOrderTimer);

      this.loadOrderTimer = setTimeout(() => {
        axios
          .post(
            API_BASE_URL + "/api/orders",
            {
              ...this.formData,
            },
            {
              params: {
                userAccessKey: this.$store.state.userAccessKey,
              },
            }
          )
          .then((response) => {
            this.$store.commit("resetCart");
            this.$store.commit("updateOrderInfo", response.data);
            this.$router.push({
              name: "orderInfo",
              params: { id: response.data.id },
            });
          })
          .catch((error) => {
            this.orderLoadingFailed = true;
            this.formError = error.response.data.error.request || {};
            this.formErrorMessage = error.response.data.error.message;
          })
          .then(() => (this.orderLoading = false));
      }, 1000);
    },
  },
  computed: {
    ...mapGetters({ products: "cartDetailProducts" , totalPrice: 'cartTotalPrice'}),
  },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: "",
      orderLoading: false,
      orderLoadingFailed: false,
    };
  },
};
</script>
