<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html"> Каталог </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html"> Корзина </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Оформление заказа </a>
        </li>
      </ul>

      <h1 class="content__title">
        Заказ оформлен <span v-if="!orderInfoLoading">№ 23621</span>
      </h1>
      <button
        class="cart__button button button--primery"
        type="submit"
        v-if="orderInfoLoading"
      >
        Идет процесс...<img src="img/spinner.gif" />
      </button>
    </div>

    <section class="cart">
      <div
        class="cart__error form__error-block"
        v-if="formErrorMessage && orderInfoLoadingFailed"
      >
        <h4>Заявка не оформлена!</h4>
        <p>
          {{ formErrorMessage }}
        </p>
      </div>
      <form
        class="cart__form form"
        action="#"
        method="POST"
        v-if="!orderInfoLoading"
      >
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет
            письмо с&nbsp;деталями заказа. Наши менеджеры свяжутся с&nbsp;Вами
            в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key"> Получатель </span>
              <span class="dictionary__value">
                {{ orderData.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Адрес доставки </span>
              <span class="dictionary__value">
                {{ orderData.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Телефон </span>
              <span class="dictionary__value"> {{ orderData.phone }} </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Email </span>
              <span class="dictionary__value"> {{ orderData.email }} </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Способ оплаты </span>
              <span class="dictionary__value"> картой при получении </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li
              class="cart__order"
              v-for="item in orderData.basket.items"
              :key="item.product.id"
            >
              <h3>{{ item.product.title }}</h3>
              <b>{{ (item.quantity * item.product.price) | numberFormat }} ₽</b>
              <span>Артикул: {{ item.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>
              Итого: <b> {{ orderData.basket.items.length }} </b> товара на
              сумму <b>{{ orderData.totalPrice | numberFormat }} ₽</b>
            </p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from "@/helpers/numberFormat";
import axios from "axios";
import { API_BASE_URL } from "@/../config";

export default {
  data() {
    return {
      formError: {},
      formErrorMessage: "",
      orderInfoLoading: false,
      orderInfoLoadingFailed: false,
    };
  },
  computed: {
    order() {
      return this.orderData ? this.orderData.items : [];
    },
    orderData() {
      return this.$store.state.orderInfo;
    },
  },
  filters: { numberFormat },
  methods: {
    loadOrderInfo() {
      this.formError = {};
      this.formErrorMessage = "";
      this.orderInfoLoading = true;
      this.orderInfoLoadingFailed = false;

      clearTimeout(this.loadOrderInfoTimer);

      this.loadOrderInfoTimer = setTimeout(() => {
        axios
          .get(
            API_BASE_URL + `/api/orders/${this.$route.params.id}`,
            {
              ...this.orderData,
            },
            {
              params: {
                userAccessKey: this.$store.state.userAccessKey,
              },
            }
          )
          .then((response) => {
            this.orderData = response.data;
            console.log(response);
          })
          .catch((error) => {
            this.orderInfoLoadingFailed = true;
            this.formError = error.response.data.error.request || {};
            this.formErrorMessage = error.response.data.error.message;
          })
          .then(() => (this.orderInfoLoading = false));
      }, 1000);
    },
  },
  created() {
    if (
      this.$store.state.orderInfo &&
      this.$store.state.orderInfo.id === this.$route.params.id
    ) {
      return;
    }
    this.$store.dispatch("loadOrderInfo", this.$route.params.id);
  },
};
</script>
